  .pswp--svg .pswp__button--arrow--left:before,
  .pswp--svg .pswp__button--arrow--right:before {
    background-image: none; }
  .pswp--svg .pswp__button--arrow--left,
  .pswp--svg .pswp__button--arrow--right {
    background: none; } }

.pswp__button--grid {
  right: 84px;
  width: auto;
  height: auto;
  color: #FFF; }

@media (max-width: 789px) {
  .pswp__button--grid {
    right: 20px;
    height: 2em;
    line-height: 2em;
    width: 2em;
    margin-right: -20px; } }

.pswp__button--download {
  right: 84px;
  width: auto;
  height: auto;
  color: #FFF; }

.pswp__button--download:hover {
  color: #fff; }

@media (max-width: 789px) {
  .pswp__button--download {
    right: 10px;
    padding: 10px; } }

.pswp__button--zoom {
  display: block;
  left: 0;
  color: #FFF;
  opacity: 0;
