/*
  Arrow buttons hit area
  (icon is added to :before pseudo-element)
*/
/*

  2. Share modal/popup and links

 */
/* increase size of share links on touch devices */
.pswp--touch .pswp__share-tooltip a {
  padding: 16px 12px; }

/*

  3. Index indicator ("1 of X" counter)

 */
.pswp__top-bar .pswp__counter {
  font-size: calc(0.00417 * 100vw + 14.91667px);
  line-height: 1.2;
  opacity: 1;
  position: relative;
  height: auto;
  top: auto;
  left: auto;
  padding: 0;
  margin-left: 30px; }

@media (max-width: 739px) {
  .pswp__top-bar .pswp__counter {
    font-size: 18px; } }

@media (min-width: 1700px) {
  .pswp__top-bar .pswp__counter {
    font-size: 22px; } }

@media (max-width: 789px) {
  .pswp__top-bar .pswp__counter {
    margin-left: 10px; } }

@media (max-width: 739px) {
  .pswp__top-bar .pswp__counter {
    margin-left: 30px; } }

.pswp__top-bar .c-photoswipe-back {
  opacity: 1;
  padding: 0;
  z-index: 1; }

.pswp__top-bar .c-photoswipe-back svg {
  fill: #fff; }

/*

  5. Loading indicator (preloader)

  You can play with it here - http://codepen.io/dimsemenov/pen/yyBWoR

 */
.pswp__preloader {
  display: none;
  width: 44px;
  height: 44px;
  position: absolute;
  top: 0;
  right: 50%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  opacity: 0;
  -webkit-transition: opacity 0.25s ease-out;
  transition: opacity 0.25s ease-out;
  will-change: opacity;
  direction: ltr; }

.pswp__preloader__icn {
  width: 20px;
  height: 20px;
  margin: 12px; }

.pswp__preloader--active {
  opacity: 1; }

.pswp__preloader--active .pswp__preloader__icn {
  /* We use .gif in browsers that don't support CSS animation */
  background: url(preloader.gif) 0 0 no-repeat; }

.pswp__trigger {
  position: relative;
  cursor: pointer;
  overflow: hidden; }

.pswp__trigger:hover:before {
  opacity: 1; }

.pswp__trigger:before {
  content: '';
  position: absolute;
  width: 40px;
  height: 40px;
  display: block;
  background: url("../img/plus_dark.svg") no-repeat center;
  top: 50%;
  left: 50%;
  -webkit-transform: translate3d(-50%, -50%, 0);
  transform: translate3d(-50%, -50%, 0);
  z-index: 1;
  border-radius: 50%;
  opacity: 0;
  -webkit-transition: opacity 0.1s linear;
  transition: opacity 0.1s linear; }

.pswp--css_animation .pswp__preloader--active {
  opacity: 1; }

.pswp--css_animation .pswp__preloader--active .pswp__preloader__icn {
  -webkit-animation: clockwise 500ms linear infinite;
  animation: clockwise 500ms linear infinite; }

.pswp--css_animation .pswp__preloader--active .pswp__preloader__donut {
  -webkit-animation: donut-rotate 1000ms cubic-bezier(0.4, 0, 0.22, 1) infinite;
  animation: donut-rotate 1000ms cubic-bezier(0.4, 0, 0.22, 1) infinite; }

.pswp--css_animation .pswp__preloader__icn {
  background: none;
  opacity: 0.75;
  width: 14px;
  height: 14px;
  position: absolute;
  left: 15px;
  top: 15px;
  margin: 0; }

.pswp--css_animation .pswp__preloader__cut {
  /* 
      The idea of animating inner circle is based on Polymer ("material") loading indicator 
       by Keanu Lee https://blog.keanulee.com/2014/10/20/the-tale-of-three-spinners.html
    */
  position: relative;
  width: 7px;
  height: 14px;
  overflow: hidden; }

.pswp--css_animation .pswp__preloader__donut {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  width: 14px;
  height: 14px;
  border: 2px solid #FFF;
  border-radius: 50%;
  border-left-color: transparent;
  border-bottom-color: transparent;
  position: absolute;
  top: 0;
  left: 0;
  background: none;
  margin: 0; }

@media screen and (max-width: 1024px) {
  .pswp__preloader {
    position: relative;
    left: auto;
    top: auto;
    margin: 0;
    float: right; } }

@keyframes clockwise {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

@keyframes donut-rotate {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0); }
  50% {
    -webkit-transform: rotate(-140deg);
    transform: rotate(-140deg); }
  100% {
    -webkit-transform: rotate(0);
    transform: rotate(0); } }

/* root element of UI */
/* top black bar with buttons and "1 of X" indicator */
.pswp__top-bar,
.pswp__bottom-bar {
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  height: calc(0.01667 * 100vw + 57.66667px);
  position: absolute;
  left: 0; }

@media (min-width: 740px) {
  .pswp__top-bar,
  .pswp__bottom-bar {
    padding-right: 45px;
    padding-left: 45px; } }

@media (min-width: 1374px) {
  .pswp__top-bar,
  .pswp__bottom-bar {
    padding-right: 90px;
    padding-left: 90px; } }

@media (max-width: 739px) {
